@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap);
*{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body{font-family:'Roboto', sans-serif;background-color:#F4F4F7}h1{font-family:'Roboto', sans-serif}a.link-unstyled{color:inherit}a.link-unstyled:hover{color:inherit;text-decoration:none}.btn{padding:0.375rem 2rem}.btn.btn-primary{color:#fff;background-color:gray;border-color:gray}.btn.btn-primary:not(:disabled):not(.disabled):hover{color:gray;background-color:#ffffff;border-color:#ffffff}.btn.btn-primary:not(:disabled):not(.disabled):active{color:#fff;background-color:gray;border-color:gray}.btn.btn-primary:not(:disabled):not(.disabled):focus{box-shadow:0 0 0 0.2rem rgba(125,205,64,0.5)}.btn-primary:active{background-color:gray;border-color:gray}.btn.btn-danger{color:#fff;background-color:#CD4040;border-color:#CD4040}.btn.btn-danger:not(:disabled):not(.disabled):hover{color:#fff;background-color:#CD4040;border-color:#CD4040}.btn.btn-danger:not(:disabled):not(.disabled):active{color:#fff;background-color:#CD4040;border-color:#CD4040}.btn.btn-danger:not(:disabled):not(.disabled):focus{box-shadow:0 0 0 0.2rem rgba(255,64,64,0.5)}.row{margin:auto}

